html, body
  margin: 0
  padding: 0
  text-align: center
  height: 100%
  overflow: hidden

body
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab)
  background-size: 400% 400%
  animation: gradient 15s ease infinite
  font-family: 'Fira Code', monospace
  font-size: 100%
  position: relative
  *
    -webkit-tap-highlight-color: transparent

#canvas
  // 9:16 aspect ratio
  --w: calc(min(100vw,(9/16)*100vh))
  width: var(--w) 
  margin: auto


.splash
  margin: 4vmin auto
  cursor: pointer
  width: 100%
  height: 100%
  filter: drop-shadow(10px 10px 5px black)
  img
    width: 100%
    display: block
    margin: 0 auto

h3  
  font-size: calc(var(--w)/10)
p
  font-size: calc(var(--w)*0.05)
h3,p
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
  color: white
  margin: 0.2em


.puzzle
  cursor: pointer
  width: calc(var(--w))
  height: calc(var(--w))
  margin: 4vmin auto
  position: relative
  box-shadow: black 1vmin 1vmin 10px 0px
  display: flex
  flex-direction: column
  border: 1px solid black
  touch-action: none
  user-select: none
  .row
    flex: 1 1 0px
    display: flex
    .cell
      flex: 1 1 0px
      border: 1px solid black
      background: white
      font-size: 0rem
      svg
        font-size: 1rem
      text
        text-anchor: middle
        text-transform: uppercase
        font-size: 0.5em
  .marker
    position: absolute
    background: rgba(255,0,0,0.5)
    touch-action: none
    pointer-events: none

.words
  touch-action: none
  pointer-events: none
  span
    text-transform: uppercase
    display: inline-block
    margin: 0 1vmin
    font-size: 4vmin
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000
    color: white
    &.found
      text-decoration: line-through

button.solve
  cursor: pointer
  margin: 2vmin
  font-size: 3vmin
  text-shadow: 1px -1px 0 #000, 1px 1px 0 #000
  color: black
  background: white
  border: none
  padding: 1vmin
  border-radius: 5px

@keyframes gradient
  0%
    background-position: 0% 50%
  50%
    background-position: 100% 50%
  100%
    background-position: 0% 50%
